import React, { useEffect } from "react";
import { Piechart } from "../../funcs";
import Legends from "./Legends";
const Tokenomics = (props) => {
  const TOTAL_SUPPLY = 100000;
  var shares = {
    data: {
      "TEAM - 20%": TOTAL_SUPPLY * (20 / 100),
      "PRIVATE SALE - 20%": TOTAL_SUPPLY * (20 / 100),
      "REWARD POOL/STAKING FARMING - 40%": TOTAL_SUPPLY * (40 / 100),
      "INITIAL LIQUIDITY POOL - 15%": TOTAL_SUPPLY * (15 / 100),
      "DEVELOPMENT - 5%": TOTAL_SUPPLY * (5 / 100),
    },
    colors: ["#fde23e", "#f16e23", "#57d9ff", "#937e88", "#008"],
  };
  useEffect(() => {
    var myCanvas = document.getElementById("myCanvas");

    // using the function
    new Piechart({
      canvas: myCanvas,
      data: shares.data,
      colors: shares.colors,
    }).draw();
  }, []);
  return (
    <div className="pb-5">
      <div className="text-center" id="tokenomic">
        <h1 className="pt-4 pb-4">Tokenomics</h1>
        <Legends data={{ ...shares.data }} colors={shares.colors} />
        <canvas
          id="myCanvas"
          className="mt-4"
          style={{ width: "50%", height: "50%", margin: "0", padding: "0" }}
        ></canvas>
      </div>
      <div className="float-right mr-3">
        <p>Max Supply : 100000</p>
      </div>
    </div>
  );
};

export default Tokenomics;
